  display: flex;
  flex-direction: column;
  align-items: center;

  .loader {
    margin-bottom: 46px;
    margin-top: 12px;
  }
  &__review {
    padding: 11px 20px;
    margin: 16px 0 53px 0;

    .button__label {
      display: flex;
      align-items: center;
    }
    .icon {
      path {
        stroke: black;
      }
    }
  }
  &__email {
    position: relative;
    width: 100%;
    padding: 20px 0;
    text-align: center;

    &-notify {

    }
    .tooltip {
      position: absolute;
      left: 18px;
      top: 50%;
      transform: translateY(-50%);
      &__content {
        padding: 8px 12px;
        font-size: 12px;
        width: 125px;
      }
    }
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    .loader {
      margin-bottom: 36px;
      margin-top: 50px;
    }
    .deposit-contact-support {
      margin-bottom: 68px;
      margin-top: 50px;
    }
    &__review {
      padding: 0;
      margin: 0 0 286px 0;
      background-color: white;

      &-error {
        margin-bottom: 193px;
      }
    }
  }
}
</style>
